// start content

.intro-header .site-heading,
.intro-header .post-heading,
.intro-header .page-heading {
 color: #000;
 padding-bottom: 0;
}

.navbar-custom .navbar-brand {
 margin-bottom: 15px;
}

.intro-header {
 margin-top: 30px;
 margin-bottom: 0;
}

@media only screen and (min-width: 768px) {
 .navbar-custom .navbar-brand,
 .navbar-custom .nav li a {
 color: #000;
 margin-bottom: 0;
 }

 .intro-header {
 margin-top: 0;
 }

 .intro-header .post-heading h1 {
 font-size: 1.8em;
 }

 .intro-header .site-heading,
 .intro-header .post-heading,
 .intro-header .page-heading {
 padding: 100px 0 0;
 }

 .navbar-custom {
 background-color: rgba(48, 132, 197, 0.9) !important;
 }

 .navbar-custom .navbar-brand,
 .navbar-custom .nav li a {
 color: #fff !important;
 }

 .navbar-custom.is-fixed {
 position: fixed;
 top: -67px;

 background-color: rgba(255, 255, 255, .9);
 border-bottom: 1px solid #f2f2f2;
 }

 .navbar-custom.is-fixed .navbar-brand,
 .navbar-custom.is-fixed .nav li a {
 color: inherit;
 }
}

@media only screen and (min-width: 1170px) {
 .navbar-custom.is-fixed {
 position: fixed;
 top: -67px;
 }
}

@media only screen and (min-width: 990px) {
 div.highlighter-rouge:hover {
 margin-right: -100px;
 }
}

@media only screen and (min-width: 1200px) {
 div.highlighter-rouge:hover {
 margin-right: -150px;
 }
}

.pager li {
 max-width: 48%;
 display: inline-block;
}

.pager .previous {
 float: right;
}

.pager .next {
 float: left;
}

div.highlight {
 background-color: #f5f5f5;
}

.intro-header .post-heading .meta a {
 color: #000;
}

hr.small {
 border-color: #000;
}

article div.post li a {
 text-decoration: underline;
}

.anchor-link {
 padding-left: .25em;
 opacity: 0;
 transition: all .25s linear;
}

.anchor-link i {
 font-size: .8em;
}

.anchor-link i:before {
 vertical-align: middle;
}

.anchor-link-container:hover .anchor-link {
 visibility: visible;
 opacity: 1;
 box-sizing: border-box;
 text-decoration: none;
}

.footer-icons li {
 margin: auto 10px;
}

.footer-icons a {
 text-decoration: none;
 font-size: 2em;
}

.footer-icons svg {
 width: 40px;
 height: 40px;
}

.last-built {
 font-size: .7em;
 text-align: right;
 margin-bottom: 0;
}

.theme-toggle {
 background-color: #5bc0de;
 border-color: #46b8da;
}

.theme-toggle .theme-light {
 display: block;
}

.theme-toggle .theme-dark {
 display: none;
}

.theme-toggle .feather {
 vertical-align: middle !important;
}

/*
 * Markdown notices
 * License: https://github.com/getgrav/grav-plugin-markdown-notices
 */

.notices {
 padding: 1px 1px 1px 30px;
 margin: 15px 0;
}

.notices p {

}

blockquote.success {
 border-left-color: rgba(92, 184, 92, 0.9);
}

blockquote.info {
 border-left-color: rgba(91, 192, 222, 0.9);
}

blockquote.warning {
 border-left-color: rgba(240, 173, 78, 0.9);
}

blockquote.danger {
 border-left-color: rgba(217, 83, 79, 0.9);
}

div.post > blockquote:first-child {
 margin-top: 50px;
}

div.post > blockquote p {
 margin-top: 0;
}

section#comments > h2 {
 margin-bottom: 20px;
}

section#comments .comment {
 margin-bottom: 30px;
}

section#comments .comment-username img {
 margin-right: 10px;
}

section#comments .comment-username img:hover {
 cursor: pointer;
}

section#comments .comment-body {
 padding-left: 20px;
}

pre {
 display: inherit;
 padding: inherit;
 margin: inherit;
 line-height: inherit;
 word-break: inherit;
 word-wrap: inherit;
 color: inherit;
 background-color: inherit;
 border: inherit;
 border-radius: inherit;
 font-size: .85em;
}

div.highlighter-rouge {
 border: 1px solid #ccc;
 border-radius: 4px;
 margin: 30px 0;
}
